.footer {
    background: #15161D;
    color: #B9BABC;
    margin-top: 50px;
  }


  
  .bottom-footer {
    background: #1E1F29;
  }
  
  .footer .footer-title {
    color: #FFF;
    text-transform: uppercase;
    font-size: 18px;
    margin: 0px 0px 30px;
  }

  .footer-links{
    padding-left: 0;
  }
  
  .footer-links li+li {
    margin-top: 15px;
  }

  .footer-links li {
    list-style: none;
  }
  
  .footer-links li a {
    color: #B9BABC;
    text-decoration: none;
  }
  
  .footer-links li i {
    margin-right: 15px;
    color: #D10024;
    width: 14px;
    text-align: center;
  }
  
  .footer-links li a:hover {
    color: #D10024;
  }
  
  .copyright {
    margin: 10px 0;
    display: block;
    font-size: 12px;
  }
  
  .footer-payments li {
    display: inline-block;
    margin-right: 5px;
  }
  
  .footer-payments li a {
    color: #15161D;
    font-size: 36px;
    display: block;
  }
  
  @media only screen and (max-width: 768px) {
    .footer {
      width: 100%;
      text-align: center;
    }
  }